/* purgecss start ignore */

.lc-modal {
  font-family: var(--lc-font-family-sans);
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}

.lc-modal-flex {
    flex: 1;
  }

.lc-modal-close {
    position: absolute;
    top: 4px;
    right: 4px;
  }

.lc-modal-header {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    padding: 0;
    border: 0;
  }

.lc-modal-title {
    margin: 0;
  }

.lc-modal-actions {
    display: flex;
    border-top: 1px solid var(--lighter-gray);
    padding: 12px;
    margin: 24px -16px -24px;
  }

.lc-modal .MuiCard-root {
    min-width: 320px;
    position: relative;
  }

@media (min-width: 600px) {

.lc-modal .MuiCard-root {
      min-width: 480px
  }
    }

.ReactModal__Overlay {
  background-color: rgba(0, 0, 0, 0.5) !important;
  opacity: 0 !important;
  transition: opacity 500ms ease-in-out !important;
  z-index: 9999;
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
    opacity: 1 !important;
  }

.ReactModal__Overlay.ReactModal__Overlay--before-close {
    opacity: 0 !important;
  }

/* purgecss end ignore */
